.container {
  width: 36px;
  height: 20px;
  background: #d4d4d4;
  border: 1px solid #afafaf;
  display: inline-block;
  border-radius: 10px;
  margin: 0px;
  vertical-align: bottom;
  cursor: pointer;
  transition: all 0.2s;
}

.container.selected {
  background: #2c86ff;
  border: 1px solid #0f4b9c;
}

.knob {
  width: 16px;
  height: 16px;
  background: #000;
  position: relative;
  top: 2px;
  left: 2px;
  border-radius: 8px;
  transition: all 0.2s;
}

.container.selected .knob {
  left: 18px;
}
